import React from 'react'
const { Octokit } = require('@octokit/rest')

const octokit = new Octokit({
  auth: '501d530a159fc4fb7f946263b91fc509a11c6d85'
})

class Index extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      items: []
    }
  }

  componentDidMount() {
    this.octokit = octokit
    this.getData()
  }

  async getData() {
    const res = await this.octokit.repos.getContent({
      owner: 'ryosuzuki',
      repo: 'monitor-db',
      path: 'db.json',
      headers: {
        'If-None-Match': ''
      }
    })
    try {
      const items = JSON.parse(atob(res.data.content))
      console.log(items)
      this.setState({ items: items.map(i => i.message) })
    } catch (err) {
      console.log(err)
    }
  }

  render() {
    return (
      <div>
        <title>Monitor</title>
        <div class="ui one column centered grid">
          <div class="column">
            <h1>Monitor</h1>

          </div>
        </div>


        <h1>Hello World</h1>
        <pre>{ this.state.items }</pre>
      </div>
    )
  }
}

export default Index